/**
 * Simple2DRenderer - Basic 2D canvas renderer for entities with Sprite component
 */

import { Game } from '../game';
import { Entity } from '../core/entity';
import { Sprite, Camera2D, SHAPE_RECT, SHAPE_CIRCLE, SPRITE_IMAGE } from '../components';

export interface Simple2DRendererOptions {
    /** Background color (default: '#111') */
    background?: string;
    /** Whether to clear canvas each frame (default: true) */
    autoClear?: boolean;
}

/**
 * Simple 2D renderer that draws all entities with Sprite component.
 *
 * Can be used as a plugin via game.addPlugin() or standalone.
 *
 * @example
 * // Plugin pattern (recommended)
 * game.addPlugin(Simple2DRenderer, canvas);
 *
 * // Standalone pattern
 * new Simple2DRenderer(game, canvas);
 */
export class Simple2DRenderer {
    private canvas: HTMLCanvasElement;
    private ctx: CanvasRenderingContext2D;
    private game: Game;
    private options: Required<Simple2DRendererOptions>;
    private imageCache: Map<string, HTMLImageElement> = new Map();
    private _cameraEntity: Entity | null = null;

    constructor(game: Game, canvas: HTMLCanvasElement | string, options: Simple2DRendererOptions = {}) {
        this.game = game;

        // Accept either element or selector
        if (typeof canvas === 'string') {
            const el = document.querySelector(canvas) as HTMLCanvasElement;
            if (!el) throw new Error(`Canvas not found: ${canvas}`);
            this.canvas = el;
        } else {
            this.canvas = canvas;
        }

        const ctx = this.canvas.getContext('2d');
        if (!ctx) throw new Error('Could not get 2d context');
        this.ctx = ctx;

        this.options = {
            background: options.background ?? '#111',
            autoClear: options.autoClear ?? true
        };

        // Attach to game
        game.setRenderer(this);
    }

    /** Canvas width */
    get width(): number { return this.canvas.width; }

    /** Canvas height */
    get height(): number { return this.canvas.height; }

    /** The canvas element */
    get element(): HTMLCanvasElement { return this.canvas; }

    /** The 2D context (for custom drawing) */
    get context(): CanvasRenderingContext2D { return this.ctx; }

    /**
     * Set the camera entity to use for rendering.
     * When set, the renderer will apply camera transform (position, zoom).
     */
    set camera(entity: Entity | null) {
        this._cameraEntity = entity;
        if (entity) {
            // Update viewport size in camera component
            try {
                const cam = entity.get(Camera2D);
                cam.viewportWidth = this.canvas.width;
                cam.viewportHeight = this.canvas.height;
            } catch {
                // Entity doesn't have Camera2D
            }
        }
    }

    get camera(): Entity | null {
        return this._cameraEntity;
    }

    /**
     * Render all entities with Sprite component.
     */
    render(): void {
        const { ctx, canvas, options, game } = this;

        // Clear
        if (options.autoClear) {
            ctx.fillStyle = options.background;
            ctx.fillRect(0, 0, canvas.width, canvas.height);
        }

        // Get interpolation alpha (0-1 between physics ticks)
        const alpha = game.getRenderAlpha();

        // Get camera data if available
        let camX = 0, camY = 0, camZoom = 1;
        if (this._cameraEntity && !this._cameraEntity.destroyed) {
            try {
                const cam = this._cameraEntity.get(Camera2D);
                camX = cam.x;
                camY = cam.y;
                camZoom = cam.zoom;

                // Update viewport size in case canvas was resized
                cam.viewportWidth = canvas.width;
                cam.viewportHeight = canvas.height;
            } catch {
                // Camera entity doesn't have Camera2D
            }
        }

        // Collect entities with Sprite, sorted by layer
        const entities: Array<{ entity: any; layer: number }> = [];

        for (const entity of game.getAllEntities()) {
            // Skip destroyed entities
            if (entity.destroyed) continue;

            try {
                const sprite = entity.get(Sprite);
                if (sprite && sprite.visible) {
                    // Calculate interpolated position for smooth rendering
                    entity.interpolate(alpha);
                    entities.push({ entity, layer: sprite.layer });
                }
            } catch {
                // Entity doesn't have Sprite
            }
        }

        // Sort by layer (lower first)
        entities.sort((a, b) => a.layer - b.layer);

        // Apply camera transform
        ctx.save();
        ctx.translate(canvas.width / 2, canvas.height / 2);
        ctx.scale(camZoom, camZoom);
        ctx.translate(-camX, -camY);

        // Draw each entity
        for (const { entity } of entities) {
            this.drawEntity(entity);
        }

        ctx.restore();
    }

    /**
     * Draw a single entity.
     */
    private drawEntity(entity: any): void {
        const { ctx, game } = this;

        const sprite = entity.get(Sprite);

        // Use interpolated position for smooth rendering between physics ticks
        const x = entity.render.interpX + sprite.offsetX;
        const y = entity.render.interpY + sprite.offsetY;
        const scaleX = sprite.scaleX;
        const scaleY = sprite.scaleY;

        // Get color string
        const colorStr = game.getString('color', sprite.color) || '#fff';

        ctx.save();
        ctx.translate(x, y);
        ctx.scale(scaleX, scaleY);

        const shape = sprite.shape;

        if (shape === SHAPE_CIRCLE) {
            const radius = sprite.radius;
            ctx.fillStyle = colorStr;
            ctx.beginPath();
            ctx.arc(0, 0, radius, 0, Math.PI * 2);
            ctx.fill();
        } else if (shape === SHAPE_RECT) {
            const w = sprite.width;
            const h = sprite.height;
            ctx.fillStyle = colorStr;
            ctx.fillRect(-w / 2, -h / 2, w, h);
        } else if (shape === SPRITE_IMAGE) {
            const imageId = game.getString('sprite', sprite.spriteId);
            if (imageId) {
                const img = this.getImage(imageId);
                if (img && img.complete) {
                    const w = sprite.width || img.width;
                    const h = sprite.height || img.height;
                    ctx.drawImage(img, -w / 2, -h / 2, w, h);
                }
            }
        }

        ctx.restore();
    }

    /**
     * Get or load an image.
     */
    private getImage(src: string): HTMLImageElement | null {
        let img = this.imageCache.get(src);
        if (!img) {
            img = new Image();
            img.src = src;
            this.imageCache.set(src, img);
        }
        return img;
    }

    /**
     * Preload images for faster rendering.
     */
    preload(images: string[]): Promise<void> {
        return Promise.all(
            images.map(src => new Promise<void>((resolve) => {
                const img = this.getImage(src);
                if (img?.complete) {
                    resolve();
                } else if (img) {
                    img.onload = () => resolve();
                    img.onerror = () => resolve();
                }
            }))
        ).then(() => {});
    }
}
